import React from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import EventContainer from '../../common/EventContainer';
import Course from './Course';
import SectionTitle from './../../common/SectionTitle';
import { LEFT, RIGHT } from '../../../utils/Constants';
import TimelineBar from './../../common/TimelineBar';
import { useResume } from '../../../contexts/resume';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      alignItems: 'stretch',
      justifyItems: 'center',
      [theme.breakpoints.down('md')]: {
        justifyItems: 'space-evenly',
      },
    },
    relative: {
      position: 'relative',
    },
    clear: {
      position: 'relative',
      '*zoom': 1,
  
      "&:before": {
        content: '""',
        display: 'table',
      },
  
      "&:after": {
        content: '""',
        display: 'table',
        clear: 'both'
      }
    },
    barClass: {
      height: 100,
      top: 80,
      [theme.breakpoints.down('md')]: {
        height: 500,
        top: 20,
        zIndex: -1
      },
    },
  })
);

function Educations() {
  const classes = useStyles()
  const resume = useResume();
  const educations = resume.education;

  return (
    <Grid container className={classes.container} spacing={0} direction="column">
      <SectionTitle title='Education' />
      <div style={{ position: 'relative' }}>
        <div className={classes.clear}>
          <TimelineBar barClass={classes.barClass} />
          {educations.map((education: any, index: number) => (
            <EventContainer key={index} alignment={index % 2 === 0 ? LEFT : RIGHT}>
              <Course
                institution={education.institution}
                area={education.area}
                studyType={education.studyType}
                startDate={education.startDate}
                endDate={education.endDate}
              />
            </EventContainer>
          ))}
        </div>
      </div>
    </Grid>
  );
}

export default Educations;