import React from 'react';
import moment from 'moment';
import { useFetch } from 'react-async';
import Grid from '@material-ui/core/Grid';
import Blog from './Blog';
import SectionTitle from './../../common/SectionTitle';

function Blogs() {
  const baseUrl = 'https://blog.chunkai.me';
  const { data }: any = useFetch(`${baseUrl}/api/posts.json`, { headers: { Accept: 'application/json' } })
  const posts = data && data.data.slice(0, 4);

  return posts ? (
    <Grid id="skills" container alignContent="stretch" direction="column" justify="center" spacing={0}>
      <SectionTitle title='Articles' />
      <Grid direction="row" alignContent="stretch"  justify="space-between" xs={12} item spacing={0} container>
        {posts.map((post: any, index: number) => (
          <Blog
            md={6}
            key={index}
            xs={12}
            image={`${baseUrl}/${post.cover}`}
            title={post.title}
            day={moment(post.date).format('DD')}
            month={moment(post.date).format('MMM')}
            url={`${baseUrl}/${moment(post.date).format('YYYY/MM/DD')}/${post.slug}`}
          />
        ))}
      </Grid>
    </Grid>
  ) : null
}

export default Blogs;