import React from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import EventContainer from '../../common/EventContainer';
import Event from './Event';
import SectionTitle from './../../common/SectionTitle';
import { LEFT, RIGHT } from '../../../utils/Constants';
import TimelineBar from '../../common/TimelineBar';
import { useResume } from '../../../contexts/resume';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      alignItems: 'stretch',
      justifyItems: 'center',
      [theme.breakpoints.down('md')]: {
        alignItems: 'stretch',
        justifyItems: 'space-evenly',
      },
    },
    barClass: {
      height: 1000,
      top: 80,
      [theme.breakpoints.down('md')]: {
        height: 1300,
        top: 20,
        zIndex: -1
      },
    },
    relative: {
      position: 'relative',
    },
    clear: {
      position: 'relative',
      '*zoom': 1,
  
      '&:before': {
        content: '""',
        display: 'table',
      },
  
      '&:after': {
        content: '""',
        display: 'table',
        clear: 'both'
      }
    }
  })
);

function Experiences() {
  const classes = useStyles()
  const resume = useResume();
  const experiences = resume.work;

  return (
    <Grid container item xs={12} spacing={0} className={classes.container} direction="column" justify="center">
      <SectionTitle title='Work Experience' />
      <div style={{ position: 'relative' }}>
        <div className={classes.clear}>
          <TimelineBar barClass={classes.barClass} />
          {experiences.map((experience: any, index: number) => (
            <EventContainer key={index} alignment={index % 2 === 0 ? LEFT : RIGHT}>
              <Event
                company={experience.company}
                position={experience.position}
                startDate={experience.startDate}
                endDate={experience.endDate}
                summary={experience.summary}
                highlights={experience.highlights}
              />
            </EventContainer>
          ))}
        </div>
      </div>
    </Grid>
  );
}

export default Experiences;